import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [
    createPersistedState({
      key: 'Questions',
        storage: localStorage       
    })
  ],

  state: {
    Questions: [],
    Mistakes:[],
    UserAnswer: [],
    UserSelectedCategory: [],
    UserQuestions: [],
    DisableCheckAnswer: true,
    QuestionType: 0
  },

  mutations: {
    AddItems(state, payload){
      state.Questions.push(payload)
    },

    QuestionType(state, payload){
      state.QuestionType = payload
    },

    UserQuestions(state, payload){
      state.UserQuestions = payload
    },

    AddMistakes(state, payload){
      state.Mistakes.push(payload)
    },

    DisableCheckAnswer(state, payload){
      state.DisableCheckAnswer = payload
    },

    RemoveItems(state, payload){
      state.Questions.splice(payload, 1)
    },

    UserCategory(state, payload){
      state.UserSelectedCategory = payload
    },

    PushFromDB(state, payload){
      state.Questions = []
      state.Questions = payload
    },

    ResetUserAnswer(state){
      state.UserAnswer = []
      state.Mistakes = []
    },



    addUserAnswer(state, payload){
      for(let i = 0; i < state.UserAnswer.length; i++){
        if(payload.index == state.UserAnswer[i].index){
          state.UserAnswer.splice(i, 1)
        }
      }

      state.UserAnswer.push(payload)
    },

  },

  actions: {
    AddItems(context, payload){
      context.commit('AddItems', payload)
    },
    QuestionType(context, payload){
      context.commit('QuestionType', payload)
    },
    UserQuestions(context, payload){
      context.commit('UserQuestions', payload)
    },

    AddMistakes(context, payload){
      context.commit('AddMistakes', payload)
    },

    DisableCheckAnswer(context, payload){
      context.commit('DisableCheckAnswer', payload)
    },

    ResetUserAnswer(context){
      context.commit('ResetUserAnswer')
    },

    UserCategory(context, payload){
      context.commit('UserCategory', payload)
    },

    
  

    addUserAnswer(context, payload){
      context.commit('addUserAnswer', payload)
    },


    RemoveItems(context, payload){
      context.commit('RemoveItems', payload)
    },

    PushFromDB(context, payload){
      context.commit('PushFromDB', payload)
    }

  },
})
