<template>
  <v-card class="pa-2"  outlined v-show="(item.kind === UserSelectedCategory) ? true:false" >
    <v-list-item three-line>
      <v-list-item-content :title="item._id">
        <span @click="showid(item)" style="white-space: pre-line"><b>{{(count+1)}}.&nbsp; {{ item.question }}</b></span>
        <template v-if="item.questionpic != null || item.questionspic">
          <center><v-img max-height="800px" :width="200" max-width="400px" :src="require('@/assets/'+item.questionpic)" /></center>
        </template>
        <v-list-item-subtitle>
          <v-container class="pb-0 mb-0" >

<template v-if="QuestionType == 0">
            <v-radio-group v-model="radioGroup" class="pl-3 pr-3" dense hide-details>
              <template v-for="(option, index) in getLabel(item)"> 
                <v-radio class="pa-2" style="white-space: pre-line" :label="option" :key="option+index" :disabled="isRadioDisabled" @click="saveAnswer(count, option, item._id)" :value="option" >
                  <template #label>
                    <template v-if="option.includes('.png')">
                      <v-img :key="index+8+option" max-height="450px" max-width="450px" :src="require('@/assets/'+option)" /> 
                    </template>
                    <template v-else ref="option">
                      {{ option }}
                    </template>
                  </template>
                </v-radio>
                    <v-divider :key="index+10+option"></v-divider>
              </template>
            </v-radio-group>
</template>
            <template v-else>
                    <v-textarea
                      label="Answer"
                      v-show="isQuiz"
                      auto-grow
                      variant="outlined"
                      :disabled="disabledInput"
                      rows="1"
                      row-height="10"
                      outlined
                      hide-details
                      @keyup="saveAnswer(count, fillInAnswer, item._id)"
                      v-model="fillInAnswer"
                    ></v-textarea>

            </template>

            <template v-if="item.type == 3">
              <template v-for="(enumitem, idx) in parseInt(item.enumcount)">
                   <v-text-field :placeholder="enumitem.toString()" outlined v-model="enumAnswer.ans[idx]" :key="idx" @change="saveAnswer(idx, enumAnswer.ans[idx], item._id, 3)">
                    <template #append-outer>
                 
                          <v-icon color="green darken-5" v-show="enumAnswerIcon.isshow[idx]" >mdi-check-bold</v-icon>
                 

                    </template>

                   </v-text-field>


              </template>
         
 
            </template>
          </v-container>
        </v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
    <template v-if="item.correctanswer.includes('.png')" >
      <span :class="classCorrect" v-show="showanswer" :ref="count" > Answer:&emsp;<b>{{ (item.correctanswer.split('-')[1] ).split('.')[0] }}</b></span> 
    </template>
    <template v-else>
      <span :class="classCorrect" v-show="showanswer" :ref="count" > Answer:&emsp;<b>{{ item.correctanswer }}</b></span>
    </template>



  </v-card>
</template>
<style type="text/css">
  .text-block {
    white-space: pre;
}
</style>
<script>
import { mapState } from 'vuex';
import { bus } from '../main.js'

  export default {
    props:{
      item: Object,
      count: Number,
    },

    data () {
      return {
        radioGroup: null,
        classCorrect: null,
        showanswer: false,
        isQuiz: true,
        isRadioDisabled: false,
        enumAnswer: {
          ans: [],
        },
        enumAnswerIcon:{
          icon: [],
          isshow:[]
        },
        fillInAnswer: null,
        disabledInput: false
      }
    },

    computed:{
      ...mapState(['Questions','UserQuestions', 'UserAnswer', 'UserSelectedCategory','DisableCheckAnswer', 'QuestionType']),
    },

    methods:{
      showid(item){
        alert(item._id)
      },

      saveAnswer(count, answer, id, type){
        let tmpObj = {}

        if(type != 3){
          tmpObj.index = count
          tmpObj.answer = answer
          tmpObj.id = id 
        }else {
          tmpObj.index = count
          tmpObj.id = id
          tmpObj.answer = answer
          tmpObj.type = 3
        }

        this.$store.dispatch('addUserAnswer', tmpObj)

        if(this.DisableCheckAnswer && this.UserAnswer.length > 0){
          this.$store.dispatch('DisableCheckAnswer', false)
        }
      },

      getLabel(itemArr){
        let tmpArr = []    
        for (const [key, value] of Object.entries(itemArr)) {
          if(key.includes('answer') && !key.includes('correct') && value != 'null' && !key.includes('pic') ){
            tmpArr.push(itemArr[key]) 
          }
        } 

        return tmpArr
      }
    },
    
    mounted(){ 
      this.classCorrect = 'pa-6 blue--text'

      bus.$on('showReview', (bool) => {
        this.isQuiz = bool
        this.$store.dispatch('QuestionType', 1)
        //this.disabledInput = true
      })

      bus.$on('showanswer', () => {
        this.showanswer = true
        this.disabledInput = true
      })

      bus.$on('resetTextArea', () =>{
        this.fillInAnswer = null
        this.disabledInput = false
      })

      bus.$on('isRadioDisabled', (data) =>{
        this.isRadioDisabled = data
      })

      bus.$on('resetRadio', () =>{
        this.radioGroup = null
        this.showanswer = false
      })

      bus.$on( 'checkAnswer', (data) => {
        for(let i = 0; i < data.length; i++){
          if(this.$refs[data[i].index]){
            this.UserQuestions.forEach(item =>{

              if(item._id == data[i].id ){
                // if(item.correctanswer.includes('.png')){
                //   tmpTxt = 'Image: ' + (item.correctanswer.split('-')[1]).split('.')[0]  
                // }else{
                //   tmpTxt = item.correctanswer  
                // }

                if(data[i].type != 3){
                  const userAns = (data[i].answer.replaceAll(' ','')).toLowerCase()
                  const correctAns = (item.correctanswer.replaceAll(' ', '')).toLowerCase()

                  if(userAns !== correctAns ){
                    //this.$refs[ data[i].index].innerHTML = '<span class="pa-6 red--text" >'+tmpTxt.replace('<','&lt;').replace('>','&gt;').replace('\n','<br>')+'</span>'
                    this.classCorrect = 'pa-6 red--text text-block'
                    this.$store.dispatch('AddMistakes', item)
                    bus.$emit('addScore', 2)
                  }else{
                    this.classCorrect = 'pa-6 blue--text text-block'
                    //this.$refs[ data[i].index].innerHTML = '<span class="pa-6 blue--text" >'+tmpTxt.replace('<','&lt;').replace('>','&gt;').replace('\n','<br>')+'</span>'
                    bus.$emit('addScore', 1)
                  }
                }
              }
            })
          }
        }
      })

 
      // bus.$on( 'itemsToDelete', (data) => {
      //   this.itemsToDelete = data
      // })

      // bus.$on( 'resetSelection', () => {
      //   this.selectedRows = []
      // })

    }
  }
</script>