<template>
    <v-app>
        <v-app-bar dense app clipped-left class="elevation-2" >
            <!-- <div class="d-flex align-center"> -->
                <v-icon @click="showCategory()">mdi-information</v-icon>
                <!-- <h4 @click="showCategory()">(info)</h4> -->
            <!-- </div> -->
            <v-spacer />
            <template v-for="(request, i) in requestList" functional>
                <v-btn small :title="request.title" :key="request.id" class="mr-3" @click="menuClick(i)">
                    <v-icon >{{request.icon}}</v-icon>                
                </v-btn>
            </template>
        </v-app-bar>
        <v-main >

            <template v-if="UserQuestions.length > 0">
                <template v-for="(question, index) in UserQuestions">
                    <v-container :key="index._id" class="mb-0">
                        <table-component :item="question" :count="index" :key="index" />
                    </v-container>
                </template>
            </template>

            <br><br>
            <v-footer v-show="isFooterShown" padless dense fixed class="pl-3 pr-3" elevation="6">
                <span class="font-weight-black green--text pr-5">
                    <v-icon color="success">mdi-sticker-check</v-icon> {{score}} 
                </span>
                <span class="font-weight-black red--text pr-5"> 
                    <v-icon @click="wrongAnswers()" color="red">mdi-sticker-remove</v-icon> {{mistakes}} 
                </span>
                <span class="font-weight-black blue--text">
                    <v-icon color="primary">mdi-sticker-text</v-icon> {{totalItems}} 
                </span>
                <v-col class="text-right">
                    <v-btn small :disabled="DisableCheckAnswer" @click="getAnswer()" color="success">
                        check&nbsp;
                        <v-icon >mdi-text-box-check</v-icon>
                    </v-btn>
                </v-col>
            </v-footer>
        </v-main>
        <template>
            <v-row justify="center">
                <v-dialog v-model="dialog" persistent max-width="290" >
                    <v-card>

                        <v-card-title class="text-h6"> Select Kind </v-card-title>
                            <v-col class="d-flex">
                                <v-select @change="getTotalExamItems(); filterUserQuestion()" :items="this.Questions.map(item => item.kind)" label="Question kind" solo v-model="selectedQuestionKind" />
                            </v-col>
                        <v-card-actions>
                            {{ totalExamItems }}
                            <v-spacer />
                            <v-btn color="green darken-1" text :disabled="selectedQuestionKind ? false:true" @click="filterUserQuestion()" > Ok </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-row>
        </template>

        <template>
            <v-row justify="center">
                <v-dialog v-model="questionUriDlg" persistent max-width="290" >
                    <v-card>
                        <v-card-title class="text"> Enter Question Url </v-card-title>
                            <v-col class="d-flex">
                                <v-text-field class="text-body" solo compressed v-model="questionUri"></v-text-field>
                            </v-col>



                        <v-card-actions>
                            <v-spacer />
                            <v-btn color="green darken-1" text @click="uploadQuestionUrl()" > Ok </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-row>           
        </template>
    </v-app>
</template>
<script>
import TableComponent from './components/Table.vue';
import { bus } from './main.js';

export default {
  name: 'App',
  components:{
      TableComponent
  },

  data: () => ({
    questionUriDlg: false,
    questionUri: null,
    password: null,
    minQuestionRange: 1,
    maxQuestionRange: 5,
    conditions: 3,
    file: null,
    score: 0,
    selectedQuestionKind: null,
    mistakes: 0,
    totalItems: 0,
    requestList: [
                        {id: 1, title: "shuffle", icon:"mdi-shuffle"}, 
                        {id: 2, title: "category", icon:"mdi-shape"},
                        {id: 3, title: "toggle", icon:"mdi-eye-off"},
                        {id: 4, title: "review", icon:"mdi-book-open"},
                        {id: 5, title: "settings", icon:"mdi-cog"}  
                    ],
    dialog: true,
    isSelecting: false,
    isFooterShown: true,
    tmpUserQuestions: [],
    questionNumber: null,
    totalExamItems: null,

  }),

  

  mounted(){
    localStorage.removeItem('Questions')

    //window.location.reload('google.com')
     // let password = prompt("Password", "******");

     //      if( password == null || password == "" ){
     //        window.location.reload();
     //      }else{
     //        if(password !== '2580'){
     //          window.location.reload();
     //        }
     //      }
      this.$store.dispatch('QuestionType', 0)
      this.$store.dispatch('UserQuestions', [])
      this.$store.dispatch('ResetUserAnswer');
      this.$store.dispatch('DisableCheckAnswer', true)
 
      bus.$on( 'addScore', (data) =>{
          switch(data){
            case 1: this.score = this.score + 1; break;
            case 2: this.mistakes = this.mistakes + 1; break;  
          }
      })

      this.$CrudFunc.readData();
  },

    computed:{
        Questions(){
            return this.$store.state.Questions
        },
        UserAnswer(){
            return this.$store.state.UserAnswer
        },
        UserSelectedCategory(){
            return this.$store.state.UserSelectedCategory
        },
        DisableCheckAnswer(){
            return this.$store.state.DisableCheckAnswer
        },
        UserQuestions(){
            return this.$store.state.UserQuestions
        },
        QuestionType(){
            return this.$store.state.QuestionType
        }
    },

    methods: {
        wrongAnswers(){
            this.$store.dispatch('UserQuestions', this.$store.state.Mistakes)

            this.$store.dispatch('ResetUserAnswer')
            this.$store.dispatch('QuestionType', 0)
            bus.$emit('resetTextArea', false)
            bus.$emit('isRadioDisabled', false) 
            bus.$emit('resetRadio', false)
            
            this.score = 0
            this.mistakes = 0

        },

        uploadQuestionUrl(){
            this.questionUriDlg = false

            this.$CrudFunc.uploadURL(this.questionUri);
        },

        viewAnswer(){
            bus.$emit('showanswer') 
        },

        getTotalExamItems(){
            const tmpTotal = (this.Questions.filter( item => item.kind === this.selectedQuestionKind)).length
            this.totalExamItems = `Total: ${tmpTotal}`
        },

        showCategory(){
            alert(this.UserSelectedCategory + '\n' + this.minQuestionRange + '-' + this.maxQuestionRange)
        },

    filterUserQuestion(){
        this.dialog = false

        do{
            this.conditions = 3
            this.questionNumber = (prompt(this.selectedQuestionKind + "\n" + this.totalExamItems + "\nSelect question range (e.g. 1-10)")).replace(/\s/g, ""); 
            this.minQuestionRange = parseInt(this.questionNumber.split('-')[0])
            this.maxQuestionRange = parseInt(this.questionNumber.split('-')[1])

            if(this.minQuestionRange == 0){
                alert('cannot start with zero')
                this.conditions = this.conditions - 1;
            }

            if(this.maxQuestionRange < this.minQuestionRange){
                alert('min range is greater than max range')


                this.conditions = this.conditions - 1;
            }

            if(!this.questionNumber.match('-')){
                this.conditions = this.conditions - 1;
                alert('please add - to range (e.g. 1-10)')
            }
        }while( this.conditions < 3 )
        
   
        let tmpRelatedQuestions = [];

        for(let i = 0; i < this.Questions.length; i++){
            if(this.Questions[i].kind === this.selectedQuestionKind){
                tmpRelatedQuestions.push(this.Questions[i])
            }
        }

        let tmpArr = []

        for(let i = 0; i < tmpRelatedQuestions.length; i++){
            if(i >= (this.minQuestionRange - 1) && i <= (this.maxQuestionRange - 1)){
                tmpArr.push(tmpRelatedQuestions[i])
            }
        }

        this.tmpUserQuestions = tmpArr

        if(isNaN(tmpArr)){
           tmpArr = tmpArr.slice(0, this.questionNumber)   
        }
        
      this.totalItems = tmpArr.length
      this.$store.dispatch('UserQuestions', this.tmpUserQuestions)
      this.$store.dispatch('UserCategory', this.selectedQuestionKind)
    },

    menuClick(index){

      switch(index){
        case 0:
            this.shuffle();
            break;
        case 1: 
            this.reset();
            this.conditions = 3
            this.dialog = true; 
            break;
        case 2:
            if(this.QuestionType == 0){
                this.$store.dispatch('QuestionType', 1)
            }else{
                this.$store.dispatch('QuestionType', 0)
            }
            break;
        case 3:
            bus.$emit('showReview', false);
            this.viewAnswer();
            break;
        case 4:
            this.password = prompt("Password", "******");

            if(this.password == null || this.password == "" ){
                alert('wrong password')
            }else{
                if(this.password !== '2580'){
                    alert('wrong password')
                }else{
                    this.questionUriDlg = true
                }
            }
            break;
      }
    },

    shuffle(){
            this.$store.dispatch('ResetUserAnswer')
            this.$store.dispatch('QuestionType', 0)
            bus.$emit('resetTextArea', false)
            bus.$emit('isRadioDisabled', false) 
            bus.$emit('resetRadio', false)
            
            this.score = 0
            this.mistakes = 0

            let userQuestions = this.$CrudFunc.shuffleFisherYates(this.tmpUserQuestions);

            this.$store.dispatch('UserQuestions', [])
            this.$store.dispatch('UserQuestions', userQuestions)
    },

    reset(){
        bus.$emit('showReview', true);
        this.$store.dispatch('QuestionType', 0)
        this.$store.dispatch('ResetUserAnswer')
        bus.$emit('resetTextArea', false)
        bus.$emit('isRadioDisabled', false) 
        bus.$emit('resetRadio', false)

      this.score = 0
      this.mistakes = 0
      //this.$store.dispatch('PushFromDB', this.$CrudFunc.shuffleFisherYates(this.Questions))

      //console.log(this.tmpUserQuestions)

      let userQuestions = this.$CrudFunc.shuffleFisherYates(this.tmpUserQuestions) 
      userQuestions = userQuestions.slice(0, this.questionNumber)






      this.$store.dispatch('UserQuestions', [])
      this.$store.dispatch('UserQuestions', userQuestions)
    },

    getAnswer(){
      if(this.UserAnswer.length > 0){
        bus.$emit('checkAnswer', this.UserAnswer)
        bus.$emit('showanswer') 

        bus.$emit('isRadioDisabled', true) 

        this.$store.dispatch('DisableCheckAnswer', true)

      } else {
        alert('please answer at least one')
      }
    },
  }
};
</script>
